.topbar
{
    &.title
    {
        text-align: center;
        color: white;
        font-size: xx-large;
    }
    &.description
    {
        text-align: center;
        color: white;
        font-size: 1.25rem;
        font-weight: lighter;
        max-width: 1200px;
        width: 80%;
        margin: 0 auto;
    }
}

.topbar-wrapper
{
    display: flex;
    align-items: center;
    background-color: $bg-dark;
    flex-direction: column;
    padding: 1rem 1rem 2rem;
    z-index: 10;
    transform: scale(1);
    border-bottom: 1px solid $bg-lightest
}
