.live-response
{
    background-color: $darkmode-bg-brighter;
    &.header
    {
        display: flex;
        flex-direction: row;
        align-items: center;
        background-color: $darkmode-bg-brighter;
        border-color: 2px gray solid;
        padding: .25rem;

        >h1
        {
            font-weight: normal;
            font-size: 15px;
            margin-left: 0.5em;
            padding-right: 0.5m;
        }
    }
    &.body {
        margin: 0 .75rem;
        font-weight: bold;
    }
    & > span {
        display: block;
        padding: .5rem;
        color: $_color-client-error;
    }
}