.swagger-ui html {
  line-height: 1.15;
  /* 1 */
  -ms-text-size-adjust: 100%;
  /* 2 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
}

.swagger-ui body {
  margin: 0;
}

.swagger-ui article,
.swagger-ui aside,
.swagger-ui footer,
.swagger-ui header,
.swagger-ui nav,
.swagger-ui section {
  display: block;
}

.swagger-ui h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

.swagger-ui figcaption,
.swagger-ui figure,
.swagger-ui main {
  /* 1 */
  display: block;
}

.swagger-ui figure {
  margin: 1em 40px;
}

.swagger-ui hr {
  box-sizing: content-box;
  /* 1 */
  height: 0;
  /* 1 */
  overflow: visible;
  /* 2 */
}

.swagger-ui pre {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

.swagger-ui a {
  background-color: transparent;
  /* 1 */
  -webkit-text-decoration-skip: objects;
  /* 2 */
}

.swagger-ui abbr[title] {
  border-bottom: none;
  /* 1 */
  text-decoration: underline;
  /* 2 */
  text-decoration: underline dotted;
  /* 2 */
}

.swagger-ui b,
.swagger-ui strong {
  font-weight: inherit;
}

.swagger-ui b,
.swagger-ui strong {
  font-weight: bolder;
}

.swagger-ui code,
.swagger-ui kbd,
.swagger-ui samp {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

.swagger-ui dfn {
  font-style: italic;
}

.swagger-ui mark {
  background-color: #ff0;
  color: #000;
}

.swagger-ui small {
  font-size: 80%;
}

.swagger-ui sub,
.swagger-ui sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

.swagger-ui sub {
  bottom: -0.25em;
}

.swagger-ui sup {
  top: -0.5em;
}

.swagger-ui audio,
.swagger-ui video {
  display: inline-block;
}

.swagger-ui audio:not([controls]) {
  display: none;
  height: 0;
}

.swagger-ui img {
  border-style: none;
}

.swagger-ui svg:not(:root) {
  overflow: hidden;
}

.swagger-ui button,
.swagger-ui input,
.swagger-ui optgroup,
.swagger-ui select,
.swagger-ui textarea {
  font-family: sans-serif;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1.15;
  /* 1 */
  margin: 0;
  /* 2 */
