.auth 
{
    &-simple
    {
        display: flex;
        flex-direction: row;
        align-items: center;    
        justify-content: space-between;
        z-index: 2;
        .schemes-title 
        {
            display:none;
        }
        label {
            margin-right: .5rem;
            margin-top: .25rem;
        }
        .info
        { 
            &-wrapper
            {
                display: flex;
                align-items: center;
                z-index: 2;
                padding-left: 10px;
                position: relative;
            }
            &-inner
            {
                opacity: 0.99 !important;
                z-index: 1000 !important;
                position: absolute;
                background-color: $dialog-ux-modal-background-color !important;
                padding: 5px;
                border: $dialog-ux-modal-border-color 1px solid;
                margin-left: 30px;
                transform: scale(1);
                right: 2rem;
                top: -50%;
                color: $black;
            }
        }

        &.encode
        {
            input:not(:focus)
            {
                -webkit-text-security: disc;
            }
        }

        input
        {
            width: 100%;
            background-color: $bg-lighter;
            border: thin solid $auth-input-border;
            color: $auth-input-text;

            &:focus
            {
                outline: none;
                border: thin solid $form-focus-border;
            }

            &::placeholder 
            {
                color: $auth-input-placeholder;
            }
        }
    
    }
}

.auth-btn-wrapper
{
    padding: 10px 0;

    .btn-done {
      margin-right: 1em;
    }
}

.auth-wrapper
{
    display: flex;
    flex-direction: column;

    flex: 1;
    justify-content: flex-end;

    .authorize
    {
        margin-right: 10px;
    }
}

.auth-container
{
    margin: 0 0 10px 0;

    &:last-of-type
    {
        margin: 0;
        padding: 10px 20px;

        border: 0;
    }

    h4
    {
        margin: 5px 0 15px 0 !important;
    }

    .wrapper
    {
        margin: 0;
        padding: 0;
    }

    input[type=text],
    input[type=password]
    {
        min-width: 230px;
    }

    .errors
    {
        font-size: 12px;

        padding: 10px;

        border-radius: 4px;

        @include text_code();
    }
}

.scopes
{
    h2
    {
        font-size: 14px;

        @include text_headline();
    }
}

.scope-def
{
    padding: 0 0 20px 0;
    font-size: smaller;
}