@mixin nav-method($color)
{
    background-color: rgba($color, 0.65);

    &.selected 
    {
        background-color: rgba($color, 1.0);    
    }
}

.navigation
{
    &-wrapper
    {
        position: sticky;
        top: 0;
        width: 30vw;
        height: 100vh;
        z-index: 10;
        overflow: auto;
        background-color: $bg-light;
        padding: 1em;
    }

    &-section
    {
        padding-top: 0.5em;
        padding-bottom: 1em;
        color: $white;
    
        >.header
        {
            display: flex;
            flex-direction: row;
            align-items: center;
            
            h1
            {
                font-size: large;
                text-transform: uppercase;
            }
        }
    }

    &-section:not(:last-child)
    {
        border-bottom: 1px solid $bg-lighter;
    }

    &-link
    {

        &.tag
        {
            &.header
            {
                padding: 1px 1px 1px 4px;

                &:hover
                {
                    background-color: lighten($navigation-background, $amount: 10);
                }
                h1
                {
                    font-size: medium;
                    font-weight: normal;
                }
            }

            &.body
            {
                border-left-style: solid;
                border-left-width: thin;
                border-color: $border-reduced-white;
                margin-left: 5px;

            }
        }

        &.method
        {
            display: flex;
            flex-direction: row;
            align-items: center;
            width: 100%;
            padding-left: 10px;

            &:hover
            {
                background-color: lighten($navigation-background, $amount: 10);
            }

            h1
            {
                font-size: .875rem;
                font-weight: normal;
            }

            &.selected
            {
                h1
                {
                    font-weight: bold;
                }
            }

            
            >.deprecated
            {
                font-style: italic;
                font-size: xx-small;
                margin-left: 4px;
            }

            > .method-icon
            {
                margin-left: auto;
                border-radius: 4px;
                padding: .25rem .375rem;
                padding-top: 0;
                padding-bottom: 0;
                margin-top: 0;
                margin-bottom: 0;
                transition: all .25s ease-out;
                h1{
                    font-size: .675rem;
                    color: white;
                    font-weight: bold;
                }   

                &.post
                {
                    @include nav-method($_color-post)
                }
            
                &.put
                {
                    @include nav-method($_color-put);
                }
            
                &.delete
                {
                    @include nav-method($_color-delete);
                }
            
                &.get
                {
                    @include nav-method($_color-get);
                }
            
                &.patch
                {
                    @include nav-method($_color-patch);
                }
            
                &.head
                {
                    @include nav-method($_color-head);
                }
            
                &.options
                {
                    @include nav-method($_color-options);
                }
            
                &.deprecated
                {                
                    @include nav-method($_color-disabled);
                }
     
            }
        }
    }
}

.auth-item
{
    color: $navigation-auth-item;
    background: $navigation-background;
    margin: 0 10px 0 0;
}